.App {
  text-align: center;
}

.App-logo {
  animation: App-logo-spin infinite 20s linear;
  height: 40vmin;
  pointer-events: none;
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

.chart-container{
  display: inline-block;
}
.high-chart-holder{
  width: 50%;
  float: left;
}

.table-stats-container{
  margin: 25px;
}

.table-container{
  margin: 15px auto;
}

/**********************/
body{ 
  background:url('./ba-bg.gif') repeat;
}

h1,h2,h3,h4,h5,h6{ 
  color:#0066a5; 
  font-size:22px; 
  font-weight: normal; 
  margin:0; 
  padding:0 0 10px 0;
}
img {height: auto;max-width: 100%;vertical-align: middle;}
li,ul {list-style-type: none;}
a:hover{
  text-decoration: underline;
}
a:active, a:hover,a:focus{
  outline: 0;
}
.pgwrap{
  padding-top: 15px;
  padding-bottom: 15px;
}
.wrap{   
  padding-top: 15px;
  padding-bottom: 15px;
  max-width: 1600px;
}
.pgwrap .wrap{
  background-color: #f2f2f2;
}
.header-container{
  background: #003399;
  min-height: 72px;
}
.navbar-brand{
  padding-top: 0;
  padding-bottom: 0;
}
.navbar-brand img{
  max-height: 60px;
}
.header-container .navbar{
  padding: 0;
}
.header-container .navbar-nav li{
  position: relative;
}
.header-container .navbar-nav a{
  color: #fff;
  font-family: Arial;
  font-size: 14px;
  padding: .1rem .5rem;
}
.header-container .navbar-nav a:hover{
  color: #fff;
  text-decoration: underline;
}
.header-container .navbar-nav a .fa{
  font-size: 20px;
  margin-left: 5px;
}
.pgtitle{    
  border-bottom: 1px solid #6f87b5;
  margin-bottom: 15px;
}
.pgtitle:after{
  content: '';
  clear: both;
  height: 0;
  display: block;
}
.pgtitle h2{
  text-transform: capitalize;
  font-weight: 700;
}
.warning-message .wrap{
  padding-top:5px;
  padding-bottom:5px;
}
.warning-message{
  background: #f14545;
  color: #fff;
  font-weight:bold;
  padding:0;
  font-size:16px;
  text-align: center;
}
.walletbal-content{
  background: #fff;
  border: 1px solid #e1e4e1;
  color: #434347; 
  font-size:24px;
  padding-top:5px;
  padding-bottom:5px;
  margin-top:15px;
}
.walletbal-content .col-md-6.text-right{
   text-align:left!important;
}
.walletbal-amt-container{
  margin-bottom:10px;
}
.walletbal-content .btn{
  margin-bottom: 15px;
}
.wallet-bal-amt-txt{
  position: relative;
  vertical-align: middle;
}
.wallet-bal-amt-txt:before{
  content: '';
  background: url('./i-wallet.png') no-repeat top left;
  display: inline-block;
  height: 45px;
  width: 50px;
  vertical-align: middle;
}
.wallet-bal-amt{
  font-size:30px;
  font-weight:bold;
  padding: 0 5px;
  vertical-align: middle;
  color: #2ba745;
}
.stats-viewer-container{
  margin-bottom: 55px;
}
.stats-viewer-container:last-child{
  margin-bottom: 0;
}
.stats-box-container{
  /*border:1px solid #6f87b5;*/
  background: #f7f7f7;
  border: 1px solid #e8e8e8;
  padding:15px 20px;
  margin-bottom: 25px;
  min-height: 125px;
  text-align: center;
  -webkit-box-shadow: 1px 2px 5px 0px rgba(50, 50, 50, 0.05);
  -moz-box-shadow: 1px 2px 5px 0px rgba(50, 50, 50, 0.05);
  box-shadow: 1px 2px 5px 0px rgba(50, 50, 50, 0.05);
}

.stat-box-header {
  line-height: 18px;
}

.stat-box-value{
  font-size: 26px;
   font-weight: bold;
}
.chart-holder{
  margin-bottom: 25px;
  border: 1px solid #e8e8e8;
  -webkit-box-shadow: 1px 2px 5px 0px rgba(50, 50, 50, 0.05);
  -moz-box-shadow:    1px 2px 5px 0px rgba(50, 50, 50, 0.05);
  box-shadow:         1px 2px 5px 0px rgba(50, 50, 50, 0.05);

}

.view-details-link {
  font-size: 15px;
  text-decoration: underline;
  position: relative;
  margin-right: auto;
  float: right;
}

/**********************/

@media (min-width:640px) {
  .header-container .navbar-nav {
    flex-direction: row;
  }
  .header-container .navbar-nav li:before{
    content: '';
    position: absolute;
    top:11px;
    left:0;
    display: inline-block;
    width: 1px;
    height:16px;
    background: #1657da;
    border-right: 0px solid #001f5d;
  }
  .header-container .navbar-nav li:first-child:before{
    display: none;
  }
  .header-container .navbar-nav a{
    padding: .5rem 1rem;
  }
  .walletbal-content .col-md-6.text-right{
     text-align:right!important;
  }
  .walletbal-amt-container{
    margin-bottom:0px;
  }
  .walletbal-content .btn{
    margin-bottom:0px;
    margin-top: 5px;
  }
}
@media (min-width: 992px) {
  .pgwrap{
    padding: 15px;
  }
  .stat-box-value{
    font-size:36px;
  }
  .chart-holder{
    margin-bottom:3px;
  }
  .walletbal-content{
    font-size:24px;
  }
  .wallet-bal-amt{
    font-size:34px;
  }

}